import { detect } from 'detect-browser';
import * as localForage from 'localforage';
import Axios from 'axios';
import QS from 'query-string';
import debuggerLog from '../utils/debuggerLog';

const { API_URL, COOKIE_NAME } = process.env;
const api = Axios.create({ baseURL: API_URL });

/**
 * Finalytics Pixel Class
 */
class FinalyticsPixel {
  /**
   * Initialize
   */
  constructor(initialization = {}) {
    debuggerLog('[Finalytics Pixel] Constructor', initialization);
    this.clientId = initialization.clientId || null;
    this.device = {};
    this.params = {
      products_owned: '',
      ip: '',
      products_recommended: '',
      last_product_url: '',
      zipcode: '',
      use_distance_model: '',
      models: 'referrer',
      referrer: '',
      page: '',
    };
    this.ads = [];
  }

  /**
   * Initialize Client
   * @param clientId
   */
  init(clientId) {
    debuggerLog('[Finalytics Pixel] Init');
    this.clientId = clientId;
    this.storeId = `${COOKIE_NAME}__${clientId}`;
    this.updateDevice();
    this.runServices();
  }

  /**
   * Run Async Services
   * @returns {Promise<void>}
   */
  async runServices() {
    debuggerLog('[Finalytics Pixel] Run Services');
    await this.initAds();
    await this.saveStore();
  }

  /**
   * Rehydrate State
   */
  async rehydrate() {
    debuggerLog('[Finalytics Pixel] Rehidrate');
    const store = await localForage.getItem(this.storeId);

    if (store) {
      const data = JSON.parse(store);

      if (data.params) {
        this.params = data.params;
      }
      if (Array.isArray(data.ads)) {
        this.ads = [
          ...this.ads,
          ...data.ads,
        ];
      }
    } else {
      throw new Error('No Store');
    }
  }

  /**
   * Save Store
   * @returns {Promise<void>}
   */
  async saveStore() {
    debuggerLog('[Finalytics Pixel] Save Store');
    const data = {
      device: this.device,
      params: this.params,
      ads: this.ads,
    };
    const store = JSON.stringify(data);
    await localForage.setItem(this.storeId, store);
  }

  /**
   * Init Adds
   * @returns {Promise<void>}
   */
  async initAds() {
    debuggerLog('[Finalytics Pixel] Init Ads');
    try {
      await this.rehydrate();
    } catch (e) {
      // eslint-disable-next-line no-console
      console.error('Manage Error', e);
      await this.loadAds();
    }
    await this.replaceAds();
  }

  /**
   * Load Ads
   * @returns {Promise<void>}
   */
  async loadAds() {
    debuggerLog('[Finalytics Pixel] Load Ads');
    const { clientId, params } = this;
    const body = {
      cu_id: clientId,
      ...params,
    };
    const urlParams = QS.stringify(body);
    const request = await api.get(`/getads/?${urlParams}`);
    const { data } = request;
    await this.saveAd(data);
  }

  /**
   * Save Ad
   * @param data
   * @returns {Promise<void>}
   */
  async saveAd(data) {
    debuggerLog('[Finalytics Pixel] Sade Data');
    this.ads.push(data);
    await this.saveStore();
  }

  /**
   * Replace Ads
   * @returns {Promise<void>}
   */
  async replaceAds() {
    debuggerLog('[Finalytics Pixel] Replace Ads');
    this.ads.forEach((ad) => {
      const domEl = document.getElementById(ad.div_id);
      if (domEl) {
        domEl.innerHTML = ad.ad_html;
      }
    });
  }

  /**
   * Update Device Data
   */
  updateDevice() {
    debuggerLog('[Finalytics Pixel] Update Device');
    this.device = {
      ...detect(),
      width: window.innerWidth,
      height: window.innerHeight,
      ua: navigator.userAgent,
      date: new Date(),
    };
  }

  async updateParam(key, value) {
    const newParams = {
      ...this.params,
      [key]: value,
    };
    this.params = newParams;
    await this.saveStore();
    await this.init();
  }
}

export default FinalyticsPixel;
